import React, { useState } from 'react';
import { isMobile } from 'react-device-detect';
import PaperText from '../components/paperText';
import { Box, IconButton, makeStyles } from '@material-ui/core';
import Container from '../components/container';
import NavigationBar from '../components/navigation';
import SocialButton from '../components/socialButton';
import MessengerCustomerChat from 'react-messenger-customer-chat';
import MobileMenu from '../components/mobileMenu'
import jorL from '../assets/jorLogo.png'
import hacemosL from '../assets/ejesProv/hsf.jpg'

export default function Home() {
	const styles = useStyles();

	return (
		<Container>
			<MessengerCustomerChat
				pageId={process.env.REACT_APP_PAGE_FB_ID}
				appId={process.env.REACT_APP_FB_ID}
				htmlRef='https://jorweb.herokuapp.com/'
			/>
			<div style={{ position: 'absolute', top: isMobile ? 50 : 0 }}>
				{!isMobile ? <SocialButton /> : <MobileMenu />}
			</div>
			{isMobile && <div  style={{ position: 'absolute', top:  30, left: 100 } } >
				
				<img src={jorL} style={{height: 82, marginBottom: 10}} />
			</div> }
			
		

			<div className={styles.descJor}>
			
				
				{!isMobile &&<Box  border={1} onClick={() => window.open('https://mudallel.hacemossantafe.com')}  style={{
					borderRadius: '25%',
					padding: 3,  
					borderColor: '#f2f2f2', 
					position: 'absolute', 
					right: 80, 
					top: 80, 
					cursor: 'pointer',	
					}} ><img src={hacemosL} style={{height: 130, borderRadius: '25%'}} /> </Box>  }
				{/* {!isMobile && <img src={jorL} style={{height: isMobile ? 85 : 150, marginBottom: 15}} />} */}
				
				<PaperText />
				
				
			
			</div>
			{!isMobile && <NavigationBar />}
		</Container>
	);
}

const useStyles = makeStyles((theme) => ({
	typography: {
		padding: theme.spacing(2),
	},

	descJor: {
		flexDirection: 'row',
		//marginTop: isMobile ? '85%' : '15%',
		marginBottom: isMobile ? '17%' : '15%',
		width: isMobile ? '95%' : '36%',
		maxWidth: 480,
		marginLeft: isMobile ? 'auto' : '10%',
		marginRight: isMobile ? 'auto' : null,
	},
}));
