


import { Paper } from "@material-ui/core";
import { isMobile } from "react-device-detect";
const ContentPaper = (props)=> {

    return <div style={{ 
       
        marginLeft: isMobile ? 0 : 20,
        marginRight: isMobile ? 0 : 90,
       
    }}>
        {props.children}
    </div >
}


export default ContentPaper;