import { Paper, Typography, Grow, CardMedia, Card} from '@material-ui/core';
import React, {  } from 'react'
import colores from '../../styles/colors';
import { isMobile } from 'react-device-detect';
import ambiente1 from "../../assets/ejes/ambiente1.jpg";


const AmbienteContent = () => {


    return <>
        <Card style={{marginBottom: 10}}>
            <CardMedia  image={ambiente1} style={{ height: 140, borderRadius: 5 }} title={'medio ambiente'}  />
        </Card>
    {/*   <img src={ambiente1} style={{ width: '100%', borderRadius: 10 }} /> */}
    <Grow in={true} timeout={500}>       
        <Paper style={{padding: 20,margin: 'auto',marginBottom: 40, width: isMobile ? '100%' : '80%',textAlign: isMobile ? 'center' : 'justify', backgroundColor: colores.cardGrIco2}}>
            <Typography style={{fontSize: isMobile ? 18 : 24, letterSpacing: 1.4}} >
                Trabajo a través de diferentes proyectos y campañas en la construcción de una conciencia colectiva en materia de reciclaje, 
                reutilización y cuidado de nuestro ambiente, como eje fundamental para el desarrollo sustentable de nuestra ciudad.
            </Typography>
        </Paper>
    </Grow>
    </>
}


export default AmbienteContent;