import React, {  } from 'react'
import { Card, Typography, Grid, CardContent, Paper, Grow, CardMedia } from '@material-ui/core';
import colores from '../../styles/colors';
import { isMobile } from 'react-device-detect';
import genero1 from "../../assets/ejes/genero1.jpg";



const GeneroContent = () => {


    return <>
    <Card style={{marginBottom: 10}}>
        <CardMedia  image={genero1} style={{ height: 140, borderRadius: 5 }} title={'deportes'}  />
    </Card>

<Grow in={true} timeout={500}>       
    <Paper style={{padding: 20,margin: 'auto',marginBottom: 40, width: isMobile ? '100%' : '80%',textAlign: isMobile ? 'center' : 'justify', backgroundColor: colores.cardGrIco2}}>
        <Typography style={{fontSize: isMobile ? 18 : 24, letterSpacing: 1.4}} >
        Desde mi lugar, formo parte de la lucha hacia la paridad, inclusión, empoderamiento e igualdad de oportunidades para mujeres e identidades disidentes de nuestra ciudad.
        </Typography>
    </Paper>
</Grow>
</>
}


export default GeneroContent;


