import { Paper, Typography, Grow, CardMedia, Card} from '@material-ui/core';
import React, {  } from 'react'
import colores from '../../styles/colors';
import { isMobile } from 'react-device-detect';
import barrios1 from "../../assets/ejes/barrios1.jpg";


const BarriosContent = () => {


    return <>
        <Card style={{marginBottom: 10}}>
            <CardMedia  image={barrios1} style={{ height: 140, borderRadius: 5 }} title={'deportes'}  />
        </Card>
   
    <Grow in={true} timeout={500}>       
        <Paper style={{padding: 20,margin: 'auto',marginBottom: 40, width: isMobile ? '100%' : '80%',textAlign: isMobile ? 'center' : 'justify', backgroundColor: colores.cardGrIco2}}>
            <Typography style={{fontSize: isMobile ? 18 : 24, letterSpacing: 1.4}} >
            El BARRIO es una de las herramientas fundamentales hacia la inclusión, formación y contención de niños,
            niñas y adolescentes. El rol que ocupan debe ser reivindicado y defendido desde acciones concretas que los auxilien.
            </Typography>
        </Paper>
    </Grow>
    </>
}


export default BarriosContent;